 la estructura del Estado colombiano. No se trata de simples enunciados teóricos, sino de fundamentos operativos que deben aplicarse en todas las decisiones públicas, en la creación de normas, en la ejecución de políticas, en la resolución de conflictos y en la organización institucional. Su observancia es condición indispensable para el respeto del Estado Social de Derecho y para la vigencia efectiva de la democracia, la legalidad y los derechos humanos en el país.")])]),_c('div',{staticClass:"col-lg-3",attrs:{"data-aos":"zoom-in"}},[_c('img',{staticClass:"img-fluid mx-auto mx-lg-0 ms-lg-auto mb-4 mb-lg-0",staticStyle:{"max-width":"486px"},attrs:{"src":require("@/assets/curso/tema1/11.svg"),"alt":""}})])])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_1_2","data-aos":"flip-up"}},[_c('h2',[_c('span',[_vm._v("1.2")]),_vm._v(" Fines esenciales del Estado colombiano")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 order-2"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema1/17.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8 order-1 mb-3 mb-lg-0"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("Estos fines configuran un mandato constitucional vinculante que orienta las decisiones del legislador, del ejecutivo, de la administración pública y del poder judicial. No se trata de metas separadas, sino de elementos interdependientes que deben ser considerados de manera integral. Servir a la comunidad, garantizar los derechos, promover la prosperidad, mantener la integridad territorial y asegurar la convivencia son responsabilidades compartidas por todo el aparato estatal, que deben guiar la formulación de políticas, la asignación de recursos, la evaluación del desempeño institucional y el diseño de mecanismos de rendición de cuentas (Vi